body{
    min-height: 100vh;
display:grid;
place-items: center;
}
html{
    font-size: small;
}
.center-div{
    width:80vw;
    /* margin:auto; */
    display: grid;
    grid-template-columns: repeat(2,1fr);
}
.left-side,
.right-side {
    height: 80vh;
    border: 1px solid black;
    font-size:25px;
    padding-left: 2px;
}
.right-side {
    /* width:100%; */
    height: 80.4vh;
}
p {
    margin-top: 2px;
}
h1 ,h2 ,h3,h4,p{
    margin: 0;
}
.left-side{
    background-color: black;
    color:aliceblue
}
h1.heading {
    text-align: center;
    /* padding: 1rem; */
    color: crimson;
    font-family: system-ui;
    padding-bottom: 1rem;
    text-decoration: underline;
}
@media (max-width: 768px) {
    /* .center-div{
        width: 80vw;
        margin: 100px auto;
        display: grid;
        grid-template-columns: repeat(2,1fr);
        display:block;
    } */
    .center-div{
        width:90vw;
    }
 
    .right-side {
        /* width:100%; */
        height: 80.4vh;
    }
}

